<template>
  <n-space style="margin-top: 10px">
    <n-input placeholder="输入委托人员姓名" v-model:value="search.name" size="small">
      <template #prefix>
        <n-icon >
          <SearchCircleOutline />
        </n-icon>
      </template>
    </n-input>

    <n-input placeholder="输入联系电话" v-model:value="search.demandPhone" size="small">
      <template #prefix>
        <n-icon >
          <SearchCircleOutline />
        </n-icon>
      </template>
    </n-input>




    <n-button type="info" ghost   @click="findBy"  size="small"><n-icon size="20">
      <SearchCircleOutline />
    </n-icon >搜索</n-button>
    <n-button type="warning"  ghost  @click="clearSearch" size="small"><n-icon size="20">
      <TrashOutline />
    </n-icon >清空选择条件</n-button>
    <n-button type="warning"  disabled  @click="delAllSelect" size="small"><n-icon size="20">
      <TrashOutline />
    </n-icon >删除选中的数据</n-button>


  </n-space>
  <n-data-table :columns="columns" :data="data" :row-key="dataKey" style="margin-top: 10px"/>
  <div style="display: flex;flex-direction: row-reverse;margin-top: 20px">
    <n-pagination :item-count="itemCount" :page-sizes="[5, 10, 15]" showSizePicker :on-update:page="onChangePage"
                  :on-update:page-size="onChangePageSize"
    />
  </div>
</template>

<script>
import {h, onMounted, reactive, ref} from 'vue'
import {NButton, NText, NAvatar, useMessage, NTime, NDatePicker,useDialog} from 'naive-ui'
import {getRequest, putRequest} from "../../api/request";
import {useRouter} from 'vue-router'
import {SearchCircleOutline,PersonAddOutline,TrashOutline} from '@vicons/ionicons5'

const createColumns=({examine,del})=>{
  return[
    {
      type:'selection',
      fixed:'left',
      key: ''

    },
    {
      title:'委托ID',
      key:'actions',
      render(row){
        return h(
            NText,
            {
            },
            {default:()=>row.id}
        )
      }
    },
    {
      title:'委托人称呼',
      key:'actions',
      render(row){
        return h(
            NText,
            {
            },
            {default:()=>row.call}
        )
      }
    },
    {
      title:'委托人名称',
      key:'actions',
      render(row){
        return h(
            NText,
            {
            },
            {default:()=>row.user.name}
        )
      }
    },
    {
      title: '头像',
      key: 'actions',
      render(row){
        return h(
            NAvatar,{
              size:'large',
              src:row.user.userface

            },
            {
              // default:()=>row.user.userface
            }
        )
      }
    },
    {
      title: '委托地址',
      key: 'actions',
      render(row){
        return h(
            NText,
            {

            },
            {
              default:()=>row.location+row.community+row.building+"栋"+row.door+"房"
            }
        )
      }
    },
    {
      title: '联系电话',
      key: 'demandPhone'

    },
    {
      title: '创建时间',
      key: 'actions',
      render(row){
        return h(NDatePicker,{

          value:row.createDate,
          type:'datetime'
        })
      }
    },
    {
      title: '初步审核',
      key: 'actions',
      render(row){
        return h(
            NButton,{
              size:'tiny',
              type:'primary',
              onClick:()=>examine(row)
            },
            {
              default:()=>"初步审核"
            }
        )
      }

    },
    {
      title: '删除',
      key: 'actions',
      render(row){
        return h(
            NButton,{
              size:'tiny',
              type:'error',
              onClick:()=>del(row)
            },
            {
              default:()=>"删除"
            }
        )
      }
    }
  ]
}
export default {
  components:{
    SearchCircleOutline,PersonAddOutline,TrashOutline
  },
  name: "Customer",
  setup(){
    const  data=ref([])
    const router=useRouter()
    const va=ref(null)
    const itemCount=ref()
    const message=useMessage()
    const dialog=useDialog()


    const pagination=reactive({
      page:1,
      pageSize:5,
    })
    const search=reactive({demandPhone:'',name:''})


    // 该页面的数据
    function onChangePage(page){
      pagination.page=page
      getAll();
      // message.success(page)
    }
    function onChangePageSize(pageSize){
      pagination.pageSize=pageSize
      getAll();
      // message.success(pageSize)
    }
    function clearSearch() {
      search.name='';
      search.demandPhone='';
    }
    function getAll(){
      getRequest('/rent/findAll/?page='+pagination.page+'&&size='+pagination.pageSize).then(resp=>{
        data.value=resp.data.data;
        itemCount.value=resp.data.data.total;
      })
    }
    function findBy() {
      let url='/rent/findAll/?page='+pagination.page+'&&size='+pagination.pageSize;
      if (search.demandPhone){
        url+='&&demandPhone='+search.demandPhone;
      }
      if (search.name){
        url+='&&name='+search.name;
      }
      getRequest(url).then(resp=>{
        data.value=resp.data.data;
        itemCount.value=resp.data.data.total;
      })


    }
   function delAllSelect(){

    }
    onMounted(getAll)
    return{
      dataKey:(data)=>{
       return  data.key=data.id
      },
      delAllSelect,
      clearSearch,
      findBy,
      search,
      itemCount,
      onChangePageSize,
      onChangePage,
      va,
      op:[],
      getAll,

      data,
      columns:createColumns(
          {
            examine(row){
              router.push({
                path:'/customer/examine',
                query:{
                  data:row.id
                }
              })
            },
            del(row){
              dialog.warning({
                title: '警告',
                content: '你确定？',
                positiveText: '确定',
                negativeText: '不确定',
                onPositiveClick: () => {

                  putRequest("/rent/updateAndSend/"+row.id+"/"+row.uid
                  ).then(resp=>{
                    if (resp){
                      message.success("已经成功删除并通知用户！")
                      getAll()
                    }
                    console.log(resp)
                  })
                },
                onNegativeClick: () => {
                  message.error('不确定')
                }})

            }
          }
      )

    }
  }
}
</script>

<style scoped>

</style>
